/* General App Styling */

body {
    background-color: lightslategray;
    color: white;
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
    text-shadow: 1px 1px #00467f;
}

h1, h2, h3, h4, h5, h6 {
    color: white;
}

hr {
    border-width: 2px;
}


/* General Form Styling */

.genFormSettings {
    margin: 20px;
}

.form-control {
    border-radius: 4px;
    box-shadow: unset;
    font-size: 15px;
    height: 27px;
    padding: 5px 7px;
}

label {
    font-size: 15px;
    margin-bottom: 3px;
}

input {
    text-transform: capitalize;
}

.selectStyling {
    height: 27px !important;
    padding: 3px;
}

select:invalid {
    color: #999 !important;
}

option:disabled {
    color: #999 !important;
}


/* General Button Styling */
  
.genAppBtn {
    background-color: #00467f;
    border: none;
    border-radius: 4px;
    color: white;
    font-size: 15px;
    margin-top: 10px;
    outline: none;
    padding: 10px 20px;
    min-width: 125px;
}
  
.genAppBtn:hover {
    background-color: #00547f;
}
  
.genAppBtn:focus {
    outline: none;
}
